header {
  width: 100%;
  flex-wrap: nowrap;
  margin-top: 30px;
  margin-bottom: 60px;
  @include position(flex-start,center);
    .wrapperNav > nav > ul {
      display: flex;
      padding-left: 0;
      margin-bottom: 0;
      margin-top: 10px;
    }
    .wrapperNav > nav > ul > li {
      list-style-type: none;
      padding-left: 50px;
    }
    .wrapperNav > nav > ul > li:first-child {
      padding-left: 0;
    }

    .wrapperNav > nav > ul > li > a {
      text-transform: capitalize;
    }

}
